.calendar {
  padding: 50px;
  font-family: 'Open Sans', sans-serif;
}

.time {
  position: relative;

  .hour {
    height: 60px;
    border-top:  1px solid gray;
    color: gray;
  }

  .label,
  .label-minute {
    display: block;
    transform: translate(-50px,-9px);
    width: 40px;
    text-align: right;
  }

  .label-minute {
    transform: translate(-50px,7px);
    color: gray;
    font-size: 12px;
  }

  .events {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .event {
      position: absolute;
      width: 200px;
      background-color: #E2ECF5;
      border-left: 4px solid #6E9ECF;
      min-height: 10px;
      font-size: 16px;
      line-height: 1;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      padding: 2px;
      box-sizing: border-box;
    }

    .small {
      font-size: 12px;
    }
  }
}
