.calendar{padding:50px;font-family:'Open Sans',sans-serif}.time{position:relative}.time .hour{height:60px;border-top:1px solid gray;color:gray}.time .label,.time .label-minute{display:block;transform:translate(-50px,-9px);width:40px;text-align:right}.time .label-minute{transform:translate(-50px,7px);color:gray;font-size:12px}.time .events{position:absolute;top:0;left:0;right:0;bottom:0}.time .events .event{position:absolute;width:200px;background-color:#e2ecf5;border-left:4px solid #6e9ecf;min-height:10px;font-size:16px;line-height:1;text-overflow:ellipsis;overflow:hidden;white-space:nowrap;padding:2px;box-sizing:border-box}.time .events .small{font-size:12px}
